@import url(https://fonts.googleapis.com/css?family=Poppins:400,700|Quicksand:400,700&display=swap);
/* fonts */

/* variables colors */
:root {
    --main-gray-color: #919195;
    --main-blue-color:#259B9D;
    --main-yellow-color: #FFD54C;
}
/* Text size  */
h1 {
    font-size: 8vw;
    font-family: 'Poppins';
}
h2 {
    font-size: 5vw;
    font-weight: bold;    
    font-family: 'Poppins';
    padding: auto;
    color: #259B9D;
    color: var(--main-blue-color);
}
h3 {
    font-weight: bold;
    font-family: 'Poppins';
}
h4 {
    color: #259B9D;
    color: var(--main-blue-color);
    font-weight: bold;
    font-family: 'Poppins';
    font-size: 22px;
    
}
p {
    font-size: 16px;
    font-family: 'Quicksand';
    font-weight: inherit;
    padding: auto;
}
a {
    font-family: 'Quicksand';
}
hr {
    border: 2px solid;
    color: #FFD54C;
    color: var(--main-yellow-color);
    width: 100px;
    margin: 10px 0px;
    border-radius: 5px;
}
ul {
    margin-bottom: 0px;
}
* {
    margin: 0px;
    line-height: 1.3;
    padding: 0px;
    width: auto;
    text-decoration: none;
    height: auto;
    box-sizing: border-box;
    
}
#root {
    width: 100%;
    height: 100%;
}

body, html {
    width: 100%;
    height: 100%;

}
.conteiner {
    max-width: 80%;
    margin: 0 auto;
}
@media only screen and (min-width: 839px) {
    .conteiner {
        max-width: 1200px;
        margin: 0 auto;
    }
    h1 {
        font-size: 3em;
    }
    h2 {
        font-size: 2em;
    }
}


.first {
    height: 2px;
    background: var(--main-gray-color);
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
    width: 44px;
    border-radius: 2px;
    position: absolute;
}
.second{
    height: 2px;
    background: var(--main-gray-color);
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg);
    width: 44px;
    border-radius: 2px;
    position: absolute;
}
.buttonClose {
    width: 50px;
    cursor: pointer;
    position: absolute;
    height: 60px;
    padding-top: 30px;
    margin: 0px 20px 20px 220px;
}
.buttonOpen {
    display: block; 
    float: left;  
    cursor: pointer;
    margin: 26px 0px 0px 20px;
}
.menu.mobile{
    margin: 0 auto;
    text-align: center;
    height: 70px;
}
.menu.mobile img {
    margin-left: -60px;
    
}
.buttonOpen span {
    display: block;
    height: 2px;
    margin-bottom: 6px;
    z-index: 999;
    border-radius: 10px;
    width: 30px;
    background: rgb(114, 114, 114);
}
.buttonOpen span:nth-child(2){
    width: 35px;
}

.menu-mobile {
    z-index: 998;
    width: 0px;
    box-shadow: 2px 2px 40px rgba(74, 74, 74, 0.331);
    height: 100%;
    position: fixed;
    display: flex;
    left: -1000px;
    text-align: left;
    overflow: hidden;
    justify-content: left;
    flex-wrap: wrap;
    background-color: #fff
}

.menu-list {
    padding-top: 10vh;
    margin-left: 20px; 
    line-height: 2.2;
    
}
.menu-list-active {
    display: none
}
#but {
    position: absolute;
    width: 30px;
    background-color: black;
    height: 30px;
}
.menu-list hr {
    width: 40px;
    border: 2px solid
}
.menu-list h3 {
    font-size: 20px;
    color: var(--main-gray-color)
}
.menu-list a {
    color: var(--main-blue-color);
    font-size: 20px;
    font-weight: bold;
}
.footer-menu {
    width: 100%;
    justify-content: center;
    display: flex;
    flex-wrap: wrap;
}
.footer-menu img {
    margin: 0 auto;
    width: 30%;
}
.contact-info-footer {
    width: 100%;
    text-align: center;
}.contact-info-footer hr {
    margin: 0 auto;
}.contact-info-footer h3 {
    font-size: 20px;
    color: var(--main-gray-color);
}
.nav {
    width: 100%;
}
.menu {
    overflow: hidden;
    position: fixed;
    width: 100%;
    z-index: 998;
    margin-right: 10%;
    display: inline;
    background-color: #fff;
    box-shadow: 0px 2px 10px rgba(165, 164, 164, 0.399);
}
.menu a {
    float: right;
    color: gray;
    margin: 20px 0;
    text-align: center;
    padding: 10px 10px;
    opacity: 0.85;
    font-family: 'Poppins', sans-serif;
}
.menu a:nth-child(2){
    margin-right: 10%;
}
.menu a:hover{
    opacity: 1;
}
.logo {
    width: 80px;
    margin: 5px 0;
    margin-left: 10%;
}
.active {
    font-weight: bold;
}
/* header/globals */
.header-home {
    background-position: center;
    background-size: cover;
    height: 700px;
    max-width: 1300px;
    width: 100%;
    background-image:-webkit-gradient(linear, left top, left bottom, from(rgba(0,0,0,0.5)), to(rgba(0,0,0,0.45))), url(/static/media/header-mob.25b54fb0.jpg);
    background-image:-webkit-linear-gradient(rgba(0,0,0,0.5) 0%, rgba(0,0,0,0.45)), url(/static/media/header-mob.25b54fb0.jpg);
    background-image:linear-gradient(rgba(0,0,0,0.5) 0%, rgba(0,0,0,0.45)), url(/static/media/header-mob.25b54fb0.jpg);
    
}
.header-img img{
    display: none;
}
.header-info{
    bottom: 50px;
    padding: 25vw 30px;
    max-width: 100%;
}
.header-info h1,span{
    color: #fff;
}
.header-info h2{
    display: none;
    color: var(--main-yellow-color);
    font-weight: regular;
}

.heroText {
    color: rgba(255, 255, 255, 0.913);
}
span {
    color: var(--main-yellow-color);

}
p {
    color: var(--main-gray-color);
}
@media only screen and (min-width: 839px) {
.header-home {
    display: flex;
    height: 800px;
    margin: 0 auto;
    background: #fff;
    width: 100%;

}
.heroText {
    color: var(--main-gray-color);
}
.container {
    padding-left: 50px;
}
.header-img {
    padding: 0px;
    height: 500px;  
    margin-top: 100px;
    background-position: bottom;
    width: 100%;  
    background-repeat: no-repeat;
    background-image: url(/static/media/header.ef532aed.svg);
}
.header-info{
    padding: 0px;
    width: 100%;
    padding-top: 15%;
    position: inherit;
    height: 500px;

}
.header-img img {
    width: 100%;
    display: inherit;
}
.header-info h2,p{
    display: inherit;
    margin: 5px 0px; 
}
.header-info h1 {
    color: var(--main-blue-color);
    font-weight: 900;
    line-height: 1.1;
} 
}
/* section treaments */

.treamentsConteiner {
    text-align: center;
    padding-top: 30px;
}
.treamentsConteiner h2 {
    color: var(--main-blue-color);
    font-weight: bold;
}

.treamentsConteiner hr {
    margin: 0 auto;
}

.boxConteiner {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    padding: 20px 0;
}
.box {
    display: block;
    width: 350px;
    height: 440px;
    padding: 30px 30px 40px;
    margin: 20px;
    text-align: left;
    -webkit-transition-delay: 3s;
            transition-delay: 3s;
    -webkit-transition: 0.4s ease-in-out;
    transition: 0.4s ease-in-out;
    border-radius: 10px
}
.box p {
    padding: 15px 0;
}
.box hr {
    margin: 0px;
}
.box img {
    height: 100px;
}
.box h4, a {
    padding: 20px 0px;
    color: var(--main-gray-color);
}
.box:hover {
    box-shadow: 0px 0px 50px rgba(43, 43, 43, 0.084);
    -webkit-transform: scale(1.001);
            transform: scale(1.001);
}
.textContein {
    font-size: 16px;
    font-weight: normal;
}
/* section about */


.aboutConteiner{
    flex-wrap: wrap;
    position: relative;
    padding: 80px 0px;
    width: 100%;
}
.aboutConteiner img  {
    width: 100%;
    margin-left: 0px;
}
.aboutConteiner h2 {
    color: var(--main-blue-color);
}
.boxText p {
    font-size: 16px;
}
.boxText {
    background: #fff;
    margin: 0 auto;
    left: 0;
    right: 0;
    top:70vw;
    width: 90%;
    padding: 30px;
    border-radius: 5px;
    box-shadow: 0px 0px 20px #00000061;
}
@media only screen and (min-width: 839px) {
.aboutConteiner {
    height: 550px;
    margin: 0 auto;
}
.aboutConteiner img {
    width: 45%;
    padding: 25px 0px;
    margin-left: 80px; 
}
.boxText {
    background: #fff;
    position: absolute;
    width: 100%;
    max-width: 550px;
    right: 49px;
    left: auto;
    top: 150px;
    height: auto;
    padding: 30px;
    border-radius: 5px;
    box-shadow: 0px 0px 20px #00000061;
}

}
/* section map */
.sectionMap{
    background: rgba(155, 194, 212, 0.171);
    margin: 60px 0px;
    padding: 60px 0px;
}
.map {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin: 0 auto;
}
.textMap {
    width: 50%;
    margin: 10% 0px;
}
.GoogleMaps{
    width: 100%;
    min-width: 300px;
    max-width: 500px !important;
    height: 500px;
    margin-left: 40px
}
/* section team */
.ourTeam  {
    text-align: center;
}
.doctor1,.doctor2 {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}


.doctorInfo{
    margin: 0px 0px 30px;
    width: 100%;
    text-align: left
}
.doctorImg{
    width: 100%;
    max-width: 300px;
}
@media only screen and (min-width: 839px)  {
    .doctorInfo {
        width: 50%;
        margin: 10% 0px;
    }
}
.buttonGreen {
    background-color: var(--main-blue-color);
    color: #fff;
    padding: 10px 20px;
    font-size: 20px;
    border-radius: 6px;
    border: none;
    font-family: 'Poppins', sans-serif;
    box-shadow: 0px 10px 15px rgba(37, 155, 157, 0.564);
    -webkit-transition: 0.5s ease-in-out;
    transition: 0.5s ease-in-out
}
.buttonGreen:hover {
    text-decoration: none;
    color: #fff;
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    box-shadow: 0px 10px 15px rgba(37, 155, 157, 0.817);
}
.buttonGhost {
    border: 1px solid #fff;
    background-color: transparent;
    padding: 10px 20px;
    font-size: 20px;
    border-radius: 6px;
    font-family: 'Poppins', sans-serif;
    color: #fff;
    -webkit-transition: 0.5s ease-in-out;
    transition: 0.5s ease-in-out
}
.buttonGhost:hover {
    background-color: #fff;
    color: var(--main-blue-color);
    -webkit-transform: scale(2, 0.8);
            transform: scale(2, 0.8);
    text-decoration: none;

}
.buttonGhost2 {
    padding: 20px;
    color: rgb(129, 129, 129);
    font-weight: bold;
    border-radius: 5px;
    -webkit-transition: 0.6s ease-in-out;
    transition: 0.6s ease-in-out;
    box-shadow: 0px 10px 10px rgba(0, 0, 0, 0.105);
}
.buttonGhost2:hover { 
    color: #fff;
    background-color: var(--main-yellow-color);
    box-shadow: 0px 10px 15px rgba(194, 196, 88, 0.817);
    text-decoration: none;
}
.form-wrapper {
    display: flex;
    justify-content: center;
}
.form-info{
    background-image: url(/static/media/bg-form-1.c237e32f.svg);
    width: 50%;
    background-position: -15em;
    background-repeat: no-repeat;
    padding: 150px 0px 100px 10vw;
}
.form-info p {
    color: #000;
}
.form-section {
    padding: 150px 10vw 100px 100px;
    width: 50%;
    background-position: center;
    background-size: cover;
    background-image: url(/static/media/bg-form-2.86a98b2b.webp);
}
.form-section h3 {
    color: #fff;
}
.form {
    display: inline;
}
label, option {
    color: #fff;
}
input, textarea, select {
    background: none;
    resize: none;
    padding: 10px;
    width: 230px;
    margin: 5px 0px;
    border-radius: 30px;
    color: #fff;
    border: 2px solid #fff;
}
::-webkit-input-placeholder {
    color: #fff;
    padding: 5px;
}
::-moz-placeholder {
    color: #fff;
    padding: 5px;
}
:-ms-input-placeholder {
    color: #fff;
    padding: 5px;
}
::-ms-input-placeholder {
    color: #fff;
    padding: 5px;
}
::placeholder {
    color: #fff;
    padding: 5px;
}
:placeholder-shown {
    color: #fff;
}
@media only screen and (max-width: 839px) {
    .form-wrapper{
        display: block;
    }
    .form-info {
        width: 100%;
    }
    .form-section {
        width: 100%;
        padding: 40px;
    }
}
footer {
    text-align: center
}
.footer-info-wrapper{
    display: flex;
    margin: 30px;
    flex-wrap: wrap;
    text-align: left;
    justify-content: space-between;
}
.footer-info h3 {
    color: #919195;
}
.footer-info{
    margin: 20px;
}
.logo-footer{
    width: 90px;
